'use client'

import React, { useEffect, useState } from 'react'
import { motion } from 'framer-motion'
import { YOUTUBE_API_KEY } from '../api/youtube'

interface VideoDetails {
  title: string
  description: string
}

const VIDEO_ID = '-Nc621Rn5RU'

export default function Fragman() {
  const [videoDetails, setVideoDetails] = useState<VideoDetails | null>(null)

  useEffect(() => {
    const fetchVideoDetails = async () => {
      const url = `https://www.googleapis.com/youtube/v3/videos?part=snippet&id=${VIDEO_ID}&key=${YOUTUBE_API_KEY}`
      try {
        const response = await fetch(url)
        const data = await response.json()
        if (data.items && data.items.length > 0) {
          setVideoDetails({
            title: data.items[0].snippet.title,
            description: data.items[0].snippet.description,
          })
        }
      } catch (error) {
        console.error('Error fetching video details:', error)
      }
    }

    fetchVideoDetails()
  }, [])

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      className="min-h-screen bg-gray-900 text-white py-4"
    >
      <div className="container mx-auto px-4">
        <h1 className="text-4xl font-bold mb-6 text-center">Kurtlar Vadisi Fragman</h1>
        <div className="w-full max-w-6xl mx-auto aspect-w-16 aspect-h-9 mb-8">
          <iframe
            src={`https://www.youtube.com/embed/${VIDEO_ID}`}
            title="Kurtlar Vadisi Fragman"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
            className="w-full h-full rounded-lg shadow-xl"
          ></iframe>
        </div>

      </div>
    </motion.div>
  )
}

